
global.isLogin=function(){
	try{
		var suid=uni.getStorageSync('suid');
		var token=uni.getStorageSync('token');
	}catch{
		
	}
	if(suid==''||token==''){
		return false;
	}else {
		return [suid,token];
	}
}
export default {
	onLaunch: function() {
		console.log('App Launch');

		setTimeout(() => {
			uni.setTabBarBadge({
				index: 1,
				text: '31'
			});
			uni.showTabBarRedDot({
				index: 3
			});
		}, 1000);
	},
	onShow: function() {
		console.log('App Show');
	},
	onHide: function() {
		console.log('App Hide');
	}
};
